@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap");

html {
    scrollbar-gutter: stable;
    scroll-behavior: smooth;
    scroll-padding-top: 45px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
}

body {
    margin: 0 auto;
    height: 100vh;
    max-width: 2000px;
    background-image: url(./../../../public/themes/photo_2024-06-13_09-17-06.jpg);
    background-position: center;
    background-attachment: fixed;
    padding-top: 90px;

    &.open__window {
        overflow: hidden;
    }
}

nav {
    position: fixed;
    margin: 0 calc((100% - 2000px) / 2);
    top: 0;
    left: 0;
    max-width: 2000px;
    width: 100%;
    height: 90px;
    padding: 0 30px;
    border-radius: 10px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
    transition: all 0.2s ease;
    z-index: 1;

    &:hover {
        background-color: rgba(28, 28, 28, 0.65);
    }

    &:active {
        filter: drop-shadow(0px 0px 10px #777);
        background-color: rgba(28, 28, 28, 0.7);
    }

    ul {
        width: 100%;
        list-style: none;
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }

    li {
        height: 90px;
        display: flex;
        align-items: center;

        &:first-child {
            margin-right: auto;
        }

        img {
            height: 50px;
            transition: all 0.3s ease;
            filter: drop-shadow(0px 0px 10px #bd0af3);
        }

        h2 {
            height: 100%;
            padding: 0 30px;
            font-weight: 600;
            display: flex;
            align-items: center;
            color: #ffb742;
        }
    }

    a {
        height: 100%;
        padding: 0 15px;
        text-decoration: none;
        display: flex;
        align-items: center;
        color: #fff;
        transition: color 0.3s ease;
        font-size: 16px;

        &:hover {
            filter: drop-shadow(0px 0px 10px #fff);
            text-decoration: underline;
            color: #ffb742;
            background-color: #6f6f6f;
            background: rgba(41, 43, 46, 0.7);
            border-radius: 10px;
        }

        &:active {
            filter: drop-shadow(0px 0px 10px #666);
            text-decoration: underline;
            color: #b16100;
            background-color: #6f6f6f;
            background: rgba(41, 43, 46, 0.7);
            border-radius: 10px;
        }
    }
}

.nav_mini {
    height: 45px;
    background-color: rgba(28, 28, 28, 0.6);
    padding: 0px 10px;

    li {
        height: 45px;

        img {
            height: 30px;
        }
    }
}

.sidebar {
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 250px;
    z-index: 2;
    background-color: rgba(28, 28, 28, 0.5);
    backdrop-filter: blur(5px);
    box-shadow: -10px 0 10px rgba(0, 0, 0, 0.1);
    display: none;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    transition: color 1s ease;

    li {
        width: 100%;
    }

    a {
        width: 100%;
    }
}

.menu-button {
    display: none;
    height: 45px;
    font-size: 30px;
    color: #ffb742;
}

.hire-btn {
    background: #ffb742;
    padding: 6px 30px;
    margin: 3px;
    border: none;
    font-size: 16px;
    cursor: pointer;
    border-radius: 6px;
    transition: background-color 0.15s ease;

    &:hover {
        text-decoration: underline;
        background: #fb8c00;
        color: #fff;
    }

    &:active {
        text-decoration: underline;
        background: #b16100;
        color: #ccc;
    }
}

.main {
    height: 640px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    img {
        filter: drop-shadow(0px 0px 10px #fcfcfc);
        width: 200px;
        height: 200px;
    }

    h4 {
        font-size: 32px;
        color: #fff;
        font-weight: 600;
        padding: 0px 0px 40px;

        span {
            color: #ffb742;
            text-decoration: underline;
        }
    }

    p {
        &.title {
            filter: drop-shadow(0px 0px 8px #000);
            font-size: 32px;
            font-weight: 600;
            color: #fff;
            margin-top: 8px;
            padding: 0 10% 20px;
        }

        &.subtitle {
            filter: drop-shadow(0px 0px 8px #000);
            font-size: 20px;
            color: #ffb742;
            margin-top: 20px;
            padding: 0 10%;

            span {
                color: #fb8c00;
                text-decoration: underline;
            }
        }
    }
}

.guarantee {
    margin-top: 12px;
    padding: 0 50px;
    display: flex;
    flex-wrap: wrap;
    gap: 24px;

    .item {
        flex-grow: 1;
        flex-basis: 200px;
        background: rgba(41, 43, 46, 0.5);
        height: 390px;
        width: 50%;
        border-radius: 10px;
        padding: 10px 10px;
        border: 1.5px solid transparent;
        cursor: pointer;
        transition: all 0.3s ease;
        gap: 10px;

        &:hover {
            scale: 1.009;
            filter: drop-shadow(0px 0px 50px #fcfcfc);
            background: rgba(41, 43, 46, 0.7);
            border-color: #ffb742;
        }

        &:active {
            scale: 1.002;
            filter: drop-shadow(0px 0px 20px #6f6f6f);
            background: rgba(41, 43, 46, 0.6);
            border-color: #fb8c00;
        }

        .icon {
            display: flex;
            flex-direction: column;
            align-items: center;

            img {
                display: block;
                width: 250px;
                border-radius: 10px;
            }

            h3 {
                color: #ffb742;
                font-size: 28px;
                transition: all 0.3s ease;

                &:active {
                    color: #fb8c00;
                }
            }
        }

        i {
            color: #fff;
            font-size: 30px;
            transition: color 0.3s ease;
            transition: all 0.3s ease;
        }

        &:hover i {
            color: #ffb742;
        }

        &:active i {
            color: #fb8c00;
        }
    }
}

.seprator {
    font-size: 25px;
    margin: 50px 50px;
    padding: 10px 20px;
    background: rgba(200, 200, 200, 0.09);
    display: inline-block;
    color: #ffb742;
    border-radius: 12px;
}

.about {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    flex-direction: column;
    overflow-y: auto;
    overflow-x: hidden;
    opacity: 0;
    pointer-events: none;
    z-index: -1;
    transition: 0.3s;

    &.active {
        z-index: 3;
        pointer-events: auto;
        opacity: 1;
    }

    .close {
        display: flex;
        color: #ca0000;
        font-size: 3rem;
        padding: 0 10px;
        filter: drop-shadow(0px 0px 10px #ca0000);
        margin: 0 10px 0 0;

        &:hover {
            color: #ff0000;
            cursor: pointer;
        }

        &:active {
            color: #830000;
            cursor: pointer;
        }
    }

    .info {
        display: flex;
        align-items: center;
        flex-direction: column;
        padding: 30px 20%;
        filter: drop-shadow(0px 0px 15px #f5f5f5);

        h3 {
            color: #fff;
            font-size: 32px;
            font-weight: 600;
            margin-bottom: 10px;
        }

        p {
            color: #fcfcfc;
            font-size: 20px;

            a.info-a {
                text-decoration: none;
                color: #ffb742;
                transition: color 0.3s ease;

                &:hover {
                    text-decoration: underline;
                    color: #fb8c00;
                }

                &:active {
                    text-decoration: underline;
                    color: #b16100;
                }
            }
        }

        .heading {
            color: #ffb742;
            font-size: 30px;
        }

        .button__modal button {
            margin-top: 30px;
        }
    }

    img {
        width: 200px;
        height: 200px;
        border-radius: 10px;
    }
}

footer {
    position: relative;
    background: rgba(28, 28, 28, 0.7);
    padding: 80px 50px 40px;
    margin-top: 100px;
    border-radius: 15px;
    transition: all 0.3s ease;

    &:hover {
        background: rgba(28, 28, 28, 0.8);
    }

    &:active {
        background: rgba(28, 28, 28, 0.9);
    }

    .cols {
        display: flex;
        align-items: start;

        .about-col {
            flex: 3;

            h3 {
                color: #ffb742;
                font-size: 20px;
            }
        }

        .links-col {
            flex: 3;

            h4 {
                color: #fff;
                margin-bottom: 20px;
                font-size: 16px;
            }

            a {
                display: block;
                text-decoration: none;
                color: #dfdfdf;
                font-size: 14px;
                line-height: 26px;
                transition: color 0.1s ease;

                &:hover {
                    filter: drop-shadow(0px 0px 15px #ffb742);
                    text-decoration: underline;
                    color: #ffb742;
                }

                &:active {
                    filter: drop-shadow(0px 0px 8px #b16100);
                    text-decoration: underline;
                    color: #b16100;
                }
            }
        }

        .links-col-a {
            cursor: pointer;
        }
    }
}

.container {
    max-width: 50% !important;
}

.alert {
    display: flex;
    position: fixed !important;
    background-color: rgba(75, 249, 255, 0.2);
    backdrop-filter: blur(5px);
    top: 10px;
    left: 50px;
    right: 50px;
    height: 150px;
    max-width: 2000px;
    margin: 0 auto;
    text-align: center;
    transform: translateY(-150%);
    transition: 0.6s;
    z-index: 3;
    border-radius: 15px;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    font-size: 45px;
    color: #fb8c00;

    p {
        filter: drop-shadow(0px 0px 8px #000);
    }

    &.active_copy {
        transform: translateY(30%);
    }
}

@media (max-width: 2000px) {
    nav {
        margin: 0;
    }
}

@media (max-width: 780px) {
    .main {
        height: auto;

        h4 {
            font-size: 28px;
            padding: 0px 0px 30px;
        }

        p {
            &.title {
                font-size: 26px;
            }

            &.subtitle {
                font-size: 18px;
            }
        }
    }

    .about .info {
        padding: 30px 10%;

        p {
            font-size: 18px;
        }
    }

    .guarantee .item {
        height: auto;
        padding: 5px 5px;
        gap: 5px;

        .icon {
            img {
                width: 150px;
            }

            h3 {
                font-size: 20px;
                padding: 5px;
            }
        }
    }

    .seprator {
        font-size: 22px;
        margin: 30px 50px;
    }

    nav ul li a span {
        font-size: 15px;
    }

    .alert {
        top: 10px;
        left: 50px;
        right: 50px;
        height: 80px;
        border-radius: 10px;
        font-size: 30px;
    }
}

@media (max-width: 680px) {
    nav {
        padding: 0 40px;

        a {
            font-size: 28px;
        }
    }

    .hideOnMobile {
        display: none;
    }

    .menu-button {
        display: block;
    }

    .main img {
        width: 180px;
        height: 180px;
    }

    .alert {
        top: 10px;
        left: 50px;
        right: 50px;
        height: 70px;
        border-radius: 8px;
        font-size: 30px;
    }
}

@media (max-width: 600px) {
    footer {
        padding: 80px 30px 30px;
    }
}

@media (max-width: 580px) {
    .main {
        img {
            width: 150px;
            height: 150px;
        }

        h4 {
            font-size: 26px;
            padding: 0px 0px 30px;
        }

        p.title {
            font-size: 24px;
            padding: 0 5% 20px;
        }
    }

    .seprator {
        font-size: 20px;
        margin: 20px 40px;
    }
}

@media (max-width: 500px) {
    .sidebar {
        width: 50%;
    }

    footer .cols .links-col h4 {
        font-size: 14px;
    }

    .alert {
        top: 10px;
        left: 50px;
        right: 50px;
        height: 65px;
        border-radius: 15px;
        font-size: 26px;
    }
}

@media (max-width: 480px) {
    .sidebar {
        width: 50%;
    }

    .main {
        h4 {
            font-size: 24px;
            padding: 0px 0px 30px;
        }

        p.title {
            font-size: 20px;
            padding: 0 5% 15px;
        }
    }

    .alert {
        top: 10px;
        left: 50px;
        right: 50px;
        height: 60px;
        border-radius: 8px;
        font-size: 22px;
    }

    footer {
        padding: 35px 5px 25px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

@media (max-width: 400px) {
    .main {
        h4 {
            font-size: 20px;
            padding: 0px 0px 30px;
        }

        p.subtitle {
            font-size: 16px;
            padding: 0 8%;
        }
    }

    .about {
        img {
            width: 180px;
            height: 180px;
            border-radius: 10px;
        }

        .info {
            p {
                font-size: 16px;
            }

            .heading {
                font-size: 28px;
            }

            h3 {
                font-size: 32px;
                font-weight: 600;
                margin-bottom: 10px;
                padding: 10px 0 0 0;
            }
        }
    }

    footer {
        padding: 35px 5px 25px;
        display: flex;
        flex-direction: column;
        align-items: center;

        .close {
            display: flex;
            align-items: start;
            flex-direction: column;

            .links-col h4 {
                font-size: 20px;
                padding: 25px 0 0 0;
                margin-bottom: 10px;
            }
        }
    }
}

@media (max-width: 350px) {
    nav {
        padding: 0 10px;
    }

    .sidebar {
        width: 100%;
    }

    .alert {
        top: 10px;
        left: 50px;
        right: 50px;
        height: 60px;
        border-radius: 8px;
        font-size: 22px;
    }
}

@media (max-width: 320px) {
    .main {
        h4 {
            font-size: 16px;
            padding: 0px 0px 15px;
        }

        p.title {
            font-size: 14px;
            padding: 0 5% 15px;
        }
    }
}

@media (max-width: 300px) {
    nav {
        padding: 0 10px;

        a {
            font-size: 24px;
        }

        li {
            img {
                height: 40px;
            }

            h2 {
                padding: 0 25px;
                font-size: 22px;
            }
        }
    }

    .main img {
        width: 120px;
        height: 120px;
    }

    .about {
        img {
            width: 150px;
            height: 150px;
        }

        .info {
            p {
                font-size: 14px;
            }

            .heading {
                color: #ffb742;
                font-size: 24px;
            }

            h3 {
                font-size: 30px;
            }
        }
    }

    .guarantee {
        padding: 0 30px;
    }
}

@media (max-width: 260px) {
    .main {
        img {
            width: 110px;
            height: 110px;
        }

        p.subtitle {
            font-size: 14px;
            padding: 0 5%;
        }
    }

    .seprator {
        font-size: 16px;
        margin: 15px 15px;
    }

    nav {
        a {
            font-size: 22px;
        }

        li h2 {
            padding: 0 20px;
            font-size: 18px;
        }
    }

    .about {
        .close {
            font-size: 2.2rem;
            padding: 0 8px;
            margin: 0 8px 0 0;
        }

        img {
            width: 120px;
            height: 120px;
        }

        .info {
            padding: 15px 5%;

            h3 {
                font-size: 25px;
            }
        }
    }

    .guarantee .item {
        i {
            font-size: 24px;
        }

        .icon {
            img {
                width: 120px;
            }

            h3 {
                font-size: 16px;
                padding: 5px;
            }
        }
    }

    footer .cols .links-col {
        h4 {
            font-size: 20px;
            padding: 22px 0 0 0;
            margin-bottom: 10px;
        }

        a {
            display: block;
            text-decoration: none;
            color: #dfdfdf;
            font-size: 12px;
            line-height: 26px;
            transition: color 0.1s ease;
        }
    }
}
